[class*="cta-box"]:not(.cta-box--content) {
	display: block;
	position: relative;
	color: white;
	background-size:cover;
	background-position:center center;
	&:before {
		background-size: cover !important;
		background-position:center center;
		content: "";
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		z-index:0;
	}
	&:hover {
		color: white;
	}
	.cta-box--three-up & {
		height: 171px;
	}
}

.cta-box--three-up,
.cta-box--two-up {
	[class*="col-"] {
		margin-bottom: 1em;
	}
}

.cta-box--content {
	position: relative;
	z-index: 2;
	hr {
		border-color: $gray-lighter;
		margin-bottom: 10px;
	}
}

.cta-box--promo {
	.btn {
		padding: 63px 0;
		@media (max-width:$screen-sm-max) {
			padding: 20px 0;
		}
	}
	&:hover {
		.btn {
			color: white;
		}
	}
	&:before {
		background:$brand-primary url(../images/cta-texture-palm-1600x140.png) no-repeat center center;
		background-size: cover;
	}
}

.cta-box--floral {
	&:before {
		background:#394a6a url(../images/cta-texture-palm-360x170.png) no-repeat center center;
	}
	h4 {
		font-weight: 300;
		font-size: 24px;
	}
	&:hover {
		color:white;
		.btn {
			color:white;
		}
	}
	&.bg-alt:before{
		background:#394a6a url(../images/cta-texture-flower-360x170.png) no-repeat center center;
	}
	&.bg-reversed {
		&:before {
			transform: scaleX(-1);
			filter: FlipH;
			-ms-filter: "FlipH";
		}
		&:hover:before {
			transform: scale(-1.1, 1.1);
		}
	}
}

.cta-box--product {
	min-height: 400px;
	&:before {
		display: none;
	}
}

.cta-box--product-details {
	text-align: center;
	text-transform: uppercase;
	position: absolute;
	left: 20px;
	bottom: 10px;
	z-index: 4;
	hr {
		border-color: white;
		max-width: 6px;
		margin: 3px auto;
	}
}