.comment-list {
  padding-left: 0;
  list-style: none; }

.comment-list ol {
  list-style: none; }

textarea {
  resize: none; }

#gforms_confirmation_message {
  padding: 4em 0;
  font-family: "Roboto Slab", serif;
  text-transform: uppercase;
  font-size: 28px; }

.validation_error {
  color: #d51b28;
  line-height: 1.2;
  margin: 0 0 13px 0; }

.gfield_description.validation_message {
  display: none; }

.gfield_label, .gform_body label {
  display: none; }

.gform_wrapper {
  margin: 0 0 30px; }
  .gform_wrapper input:not(.button), .gform_wrapper textarea, .gform_wrapper select {
    border: 1px solid #dadada;
    background: white;
    display: block;
    width: 100%;
    min-height: 50px;
    color: #888888;
    font-size: 14px;
    padding-left: 10px;
    padding-right: 30px;
    font-style: italic; }
    .gform_wrapper input:not(.button):active,
    .gform_wrapper input:not(.button):focus, .gform_wrapper textarea:active,
    .gform_wrapper textarea:focus, .gform_wrapper select:active,
    .gform_wrapper select:focus {
      border-color: black;
      outline: none; }
  .gform_wrapper select {
    height: 50px; }
  .gform_wrapper textarea {
    padding-top: 10px; }

.gfield_error input:not(.button), .gfield_error textarea, .gfield_error select {
  border-color: #d51b28; }

.gfield {
  margin: 0 0 20px; }

.gform_fields {
  list-style-type: none;
  margin: 0;
  padding: 0; }

input[type="submit"].gform_button {
  background: #223558;
  border: 1px solid transparent;
  transition: border 0.35s;
  color: white;
  width: 166px;
  height: 60px;
  font-size: 14px;
  text-transform: uppercase; }
  input[type="submit"].gform_button:hover {
    border-color: white; }

.main {
  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px; }
  @media (min-width: 768px) {
    .main {
      float: left;
      width: 100%; } }
  .sidebar-primary .main {
    position: relative;
    min-height: 1px;
    padding-left: 15px;
    padding-right: 15px; }
    @media (min-width: 768px) {
      .sidebar-primary .main {
        float: left;
        width: 66.6666666667%; } }

.sidebar {
  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px; }
  @media (min-width: 768px) {
    .sidebar {
      float: left;
      width: 33.3333333333%; } }

.nav {
  text-transform: uppercase; }
  .nav > li {
    display: inline-block;
    position: static; }
    .nav > li .fa {
      transform: rotate(180deg);
      transition: transform 0.35s ease;
      margin-left: 5px; }
      .scrolled .nav > li .fa {
        position: relative;
        top: 2px; }
    .nav > li.dropdown-open > a {
      color: #3f8878; }
      .nav > li.dropdown-open > a .fa {
        transform: rotate(0deg);
        transform: translate(0, -2px);
        transition: transform 0.35s ease; }
    .scrolled .nav > li > a {
      padding: 8px 15px 3px 15px;
      font-size: 17px; }
  .nav a {
    font-size: 18px;
    transition: color 0.35s ease; }
  .nav#menu-main-navigation {
    text-align: right;
    margin-right: -20px; }
    .scrolled .nav#menu-main-navigation {
      margin-right: 0;
      text-align: left;
      display: inline-block;
      vertical-align: top; }
      @media (max-width: 991px) {
        .scrolled .nav#menu-main-navigation {
          display: none; } }
    @media (max-width: 991px) {
      .nav#menu-main-navigation {
        display: none; } }

.nav > li > a:focus, .nav > li > a:hover {
  background: none; }

.sub-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
  position: absolute;
  z-index: -1;
  left: 0;
  background: white;
  opacity: 0;
  transition: opacity 1s ease;
  transition: max-height 2s ease;
  max-height: 0;
  overflow: hidden;
  text-align: left; }
  .dropdown-open .sub-menu {
    opacity: 1;
    z-index: 20;
