.breadcrumb {
	text-transform: uppercase;
	font-size: 12px;
	color:$link-hover-color;
	font-weight: 500;
	display: none;
	background: none;
	padding: 0;
	margin-bottom: 0;
	.page & {
		display: block;
	}
	.blue-bg & {
		display: none;
	}
	a {
		color:$link-hover-color;
	}
	.breadcrumb_last, a:hover {
		color:#888;
	}
}